import throttle from 'lodash/throttle';
import delay from '../common/delay';
import findLoginForm from '../common/dom/findLoginForm';
import { observe } from '../common/store';
import insertSelectAccountFrame from './insertSelectAccountFrame';

const insertOnChange = async processedInputs => {
  await delay(100);

  const form = findLoginForm();

  if (form) {
    [form.username, form.password]
      .filter(Boolean)
      .filter(input => !processedInputs.has(input))
      .forEach(input => {
        input.setAttribute('autocomplete', 'off');
        insertSelectAccountFrame(input);
        processedInputs.add(input);
      });
  }
};

const insertSelectAccountFrames = () => {
  const processedInputs = new WeakSet();
  const listener = throttle(() => insertOnChange(processedInputs), 1000);
  const mutationObserver = new MutationObserver(listener);
  mutationObserver.observe(document.body, { childList: true, subtree: true });
  document.addEventListener('click', listener);
  observe('authenticators', 'passwordmanagers').subscribe(listener);
};

export default insertSelectAccountFrames;
