import throttle from 'lodash/throttle';
import isFillable from '../common/dom/isFillable';
import hostAccountSimilarity from '../common/hostAccountSimilarity';
import { formDataForget, formDataGet } from '../common/runInBackground';
import getAccountsWhenLoggedIn from './getAccountsWhenLoggedIn';
import showSaveCredentialsFrame from './showSaveCredentialsFrame';

let mutationObserver;

const listenForSubmit = async accounts => {
  const formData = await formDataGet();

  if (!formData || !formData.username || !formData.password) {
    return;
  }

  const passwords = [...document.querySelectorAll('input[type=password]')];
  const filledPasswords = passwords.filter(isFillable).filter(e => e.value);
  const blankPasswords = passwords.filter(isFillable).filter(e => !e.value);

  if (filledPasswords.length) {
    console.debug(
      'a form with filled password is still visible.',
      'maybe not submitted yet.',
      'or submitted but waiting for xhr.',
      filledPasswords,
    );
    return;
  }

  if (blankPasswords.length) {
    console.debug(
      'this page contains a login form with blank password.',
      'maybe because previous login attempt failed.',
      'not saving bad credentials.',
      blankPasswords,
    );
    formDataForget();
    return;
  }

  console.debug('form submit detected.');

  const hostSimilarity = hostAccountSimilarity(location.host);

  const account = accounts
    .filter(hostSimilarity)
    .find(account => account.username === formData.username);

  if (account) {
    console.debug(
      'account already exists.',
      'ignore captured credentials.',
      account,
    );
    formDataForget();
    return;
  }

  mutationObserver && mutationObserver.disconnect();
  showSaveCredentialsFrame();
};

const captureFormSubmit = async () => {
  const accounts = await getAccountsWhenLoggedIn();
  console.debug('listening to dom changes to detect form-submit.');
  const listener = throttle(() => listenForSubmit(accounts), 1000);
  mutationObserver = new MutationObserver(listener);
  mutationObserver.observe(document.body, { childList: true, subtree: true });
  document.addEventListener('click', listener);
  document.addEventListener('submit', listener);
};

export default captureFormSubmit;
