import delay from '../common/delay';
import { autoFillInit, getCurrentTab } from '../common/runInBackground';
import orca18 from '../images/orca-18.png';

const insertSelectAccountFrame = el => {
  Object.assign(el.style, {
    backgroundSize: '18px',
    backgroundPositionX: '98%',
    backgroundPositionY: '50%',
    backgroundRepeat: 'no-repeat',
    boxShadow: 'none',
    backgroundImage: `url(${browser.runtime.getURL(orca18)})`,
  });

  // todo: stackoverflow.com/a/3910750
  el.onmousemove = async event => {
    await delay(1);
    const width = event.target.offsetWidth;
    const x = event.pageX - event.target.getBoundingClientRect().left;
    event.target.style.cursor = width - x < 25 ? 'pointer' : 'auto';
  };

  // todo: stackoverflow.com/a/3910750
  el.onclick = async event => {
    await delay(1);
    if (event.target.style.cursor === 'pointer') {
      createIframeListAccounts(el);
    }
  };
};

const getPositionPage = el => {
  const { right, bottom } = el.getBoundingClientRect();
  return { x: right - 10, y: bottom + 1 };
};

const createIframeListAccounts = async el => {
  const { x, y } = getPositionPage(el);
  const width = 340;
  const height = 160;

  const container = document.createElement('div');

  Object.assign(container.style, {
    position: 'absolute',
    zIndex: '2147483647',
    display: 'block',
    width: `${width}px`,
    height: `${height}px`,
    top: `${y}px`,
    left: `${x - width}px`,
    boxShadow: '2px 5px 25px #73787d',
  });

  const url = browser.runtime.getURL('selectAccountFrame.html');
  const iframe = document.createElement('iframe');
  // tab url is not accessible from iframes. get tab info from extension.
  const tab = await getCurrentTab();
  iframe.src = `${url}?host=${new URL(tab.url).host}`;

  Object.assign(iframe.style, {
    border: 'none',
    position: 'relative',
    height: '100%',
    width: '106% ',
    visibility: 'visible',
  });

  container.appendChild(iframe);
  document.body.appendChild(container);

  const removeIframe = () => {
    window.removeEventListener('message', receiveMessage);
    document.body.removeEventListener('click', removeIframe);
    container.remove();
  };

  document.body.addEventListener('click', removeIframe);

  const receiveMessage = event => {
    if (`${event.origin}/` !== browser.runtime.getURL('/')) {
      return;
    }

    const { action, id, isShared } = event.data;

    switch (action) {
      case 'close':
        removeIframe();
        break;
      case 'login':
        autoFillInit({ item: { id, isShared }, newTab: false, submit: true });
        removeIframe();
        break;
    }
  };
  window.addEventListener('message', receiveMessage);

  // todo: stackoverflow.com/a/3910750
  window.onresize = () => {
    const { x, y } = getPositionPage(el);
    container.style.top = `${y}px`;
    container.style.left = `${x - width}px`;
  };
};

export default insertSelectAccountFrame;
