import getFormInputs from './getFormInputs';
import getParentForm from './getParentForm';
import isFillable from './isFillable';
import isNotSearch from './isNotSearch';

const findLoginForms = () =>
  [...document.querySelectorAll('input')]
    .filter(isFillable)
    .map(getParentForm)
    .reduce((a, b) => [...new Set([...a, b])], [])
    .filter(Boolean)
    .filter(isNotSearch)
    .map(form => {
      const inputs = getFormInputs(form);
      const usernames = inputs.filter(e => e.type !== 'password');
      const passwords = inputs.filter(e => e.type === 'password');
      return { usernames, passwords };
    })
    // not a login form if too many inputs
    .filter(f => f.usernames.length + f.passwords.length < 4)
    // not a login form if no password inputs but many non-password inputs
    .filter(f => f.passwords.length || f.usernames.length < 3);

export default findLoginForms;
