import throttle from 'lodash/throttle';
import findLoginForm from '../common/dom/findLoginForm';
import hostAccountSimilarity from '../common/hostAccountSimilarity';
import { autoFillInit, getCurrentTab } from '../common/runInBackground';
import getAccountsWhenLoggedIn from './getAccountsWhenLoggedIn';

const listenForAutoFillInit = async accounts => {
  const form = findLoginForm();

  if (!form) {
    return;
  }

  if (form.username && form.username.value) {
    return;
  }

  if (form.password && form.password.value) {
    return;
  }

  const tab = await getCurrentTab();
  const hostSimilarity = hostAccountSimilarity(new URL(tab.url).host);

  const accountsForHost = accounts
    .filter(i => i.ssoEnabled)
    .filter(hostSimilarity);

  if (accountsForHost.length !== 1) {
    console.debug(
      `${accountsForHost.length} matching accounts. will not auto-fill.`,
      accountsForHost,
    );

    return;
  }

  autoFillInit({ item: accountsForHost[0], newTab: false, submit: false });
};

const autoFill = async () => {
  const accounts = await getAccountsWhenLoggedIn();
  const listener = throttle(() => listenForAutoFillInit(accounts), 1000);
  const mutationObserver = new MutationObserver(listener);
  mutationObserver.observe(document.body, { childList: true, subtree: true });
  document.addEventListener('click', listener);
  listener();
};

export default autoFill;
