const isNotSearch = element => {
  const getAttribute = s => element.getAttribute(s);
  return (
    element.querySelector('input[type=password]') ||
    !(
      ['id', 'name'].map(getAttribute).includes('q') ||
      ['id', 'class', 'name', 'placeholder', 'role', 'title']
        .map(getAttribute)
        .some(a => /search/i.test(a))
    )
  );
};

export default isNotSearch;
