import fromPairs from 'lodash/fromPairs';
import { from, ReplaySubject } from 'rxjs';
import { combineAll, debounceTime, filter, map } from 'rxjs/operators';

const store$ = new ReplaySubject(1000);

export const publish = data =>
  Object.entries(data).forEach(entry => store$.next(entry));

export const observe = (...keys) =>
  from(keys).pipe(
    map(key => store$.pipe(filter(([k]) => k === key))),
    combineAll(),
    map(fromPairs),
    debounceTime(10),
  );
