import debounce from 'lodash/debounce';
import extractFormCredentials from '../common/dom/extractFormCredentials';
import getParentForm from '../common/dom/getParentForm';
import {
  formDataForget,
  formDataGet,
  formDataUpdate,
} from '../common/runInBackground';

const state = { username: '', password: '' };

const handleEvent = async event => {
  const form = getParentForm(event.target);

  if (!form) {
    return;
  }

  const { username, password } = extractFormCredentials(form);

  // extracted `username` might be otp
  const formData = await formDataGet();
  if (
    formData &&
    formData.password && // we already captured password in a previous page
    !password && // current visible form does not have password
    username // but it has username, which actually is otp
  ) {
    formDataForget();
    return;
  }

  if (username !== state.username || password !== state.password) {
    formDataUpdate(username, password);
    state.username = username;
    state.password = password;
  }
};

const captureCredentials = () => {
  const listener = debounce(handleEvent, 100);
  window.addEventListener('change', listener);
  window.addEventListener('input', listener);
  window.addEventListener('paste', handleEvent);
  window.addEventListener('submit', handleEvent);
  window.addEventListener('keydown', e => e.code === 'Enter' && handleEvent(e));
};

export default captureCredentials;
