import sortBy from '../sortBy';
import findLoginForms from './findLoginForms';

const findLoginForm = () => {
  const forms = findLoginForms();

  if (!forms.length) {
    return;
  }

  const sorted = sortBy(
    f => f.usernames.length === 1 && f.passwords.length === 1,
    f => f.usernames.length > 1 && f.passwords.length === 1,
    f => f.usernames.length === 1 && f.passwords.length > 1,
    f => f.usernames.length === 1 || f.passwords.length === 1,
    f => f.usernames.length > 1 && f.passwords.length > 1,
  )(forms).reverse();

  const [{ usernames, passwords }] = sorted;
  return { username: usernames[0], password: passwords[0] };
};

export default findLoginForm;
