import getFormInputs from './getFormInputs';

const getParentForm = input => {
  if (input.form) return input.form;
  if (input.type !== 'password') return;
  for (let e = input; e; e = e.parentElement) {
    const inputs = getFormInputs(e);
    if (inputs.some(i => i.form)) return;
    const passwords = inputs.filter(e => e.type === 'password');
    const usernames = inputs.filter(e => e.type !== 'password');
    if (usernames.length && passwords.length) return e;
  }
};

export default getParentForm;
