import uuid from 'uuid/v4';
import { formDataForget } from '../common/runInBackground';
import buildSaveCredentialsFrame from './buildSaveCredentialsFrame';
import buildSaveCredentialsFrameContainer from './buildSaveCredentialsFrameContainer';

let container;
const containerId = uuid();

const onMessage = event => {
  if (`${event.origin}/` !== browser.runtime.getURL('/')) {
    return;
  }
  if (event.data.action === 'close') {
    formDataForget();
    removeIframe();
  }
};

const removeIframe = () => {
  window.removeEventListener('message', onMessage);
  container.remove();
};

const showSaveCredentialsFrame = () => {
  if (document.getElementById(containerId)) return;
  container = buildSaveCredentialsFrameContainer(containerId);
  container.appendChild(buildSaveCredentialsFrame());
  document.body.appendChild(container);
  window.addEventListener('message', onMessage);
};

export default showSaveCredentialsFrame;
