const buildSaveCredentialsFrame = () => {
  const iframe = document.createElement('iframe');

  iframe.src = browser.runtime.getURL('saveCredentialsFrame.html');

  Object.assign(iframe.style, {
    border: 'none',
    position: 'relative',
    height: '100%',
    width: '100% ',
    visibility: 'visible',
  });

  return iframe;
};

export default buildSaveCredentialsFrame;
