import { observe } from '../common/store';

const getAccountsWhenLoggedIn = () =>
  new Promise(resolve => {
    const subscription = observe(
      'authenticators',
      'passwordmanagers',
    ).subscribe(({ authenticators, passwordmanagers }) => {
      if (authenticators && passwordmanagers) {
        resolve([...authenticators, ...passwordmanagers]);
        setTimeout(() => subscription.unsubscribe());
      } else {
        console.debug('will not resolve until extension is logged in.');
      }
    });
  });

export default getAccountsWhenLoggedIn;
