import delay from '../delay';

const kbEnterEvent = type =>
  new KeyboardEvent(type, {
    bubbles: true,
    cancelable: true,
    code: 'Enter',
    key: 'Enter',
    keyCode: 13,
    which: 13,
  });

const pressEnterOnFormInput = async element => {
  element.dispatchEvent(kbEnterEvent('keydown'));
  element.dispatchEvent(kbEnterEvent('keypress'));
  element.dispatchEvent(kbEnterEvent('keyup'));

  if (!element.form) return;

  [...element.form.elements]
    .filter(e => e.type === 'submit')
    .forEach(e => e.click());

  await delay(1000);

  if (!document.contains(element)) return;

  const defaultPrevented = !element.form.dispatchEvent(
    new Event('submit', { bubbles: true, cancelable: true }),
  );

  await delay(1000);

  if (defaultPrevented || !document.contains(element)) return;

  HTMLFormElement.prototype.submit.call(element.form);
};

export default pressEnterOnFormInput;
