import '../common/sync';
import autoFill from './autoFill';
import autoLogin from './autoLogin';
import captureCredentials from './captureCredentials';
import captureFormSubmit from './captureFormSubmit';
import inIframe from './inIframe';
import insertSelectAccountFrames from './insertSelectAccountFrames';

insertSelectAccountFrames();

(async () => {
  const { autoFillOnLoad } = await browser.storage.local.get();
  if (autoFillOnLoad) autoFill();
})();

autoLogin();

captureCredentials();

if (!inIframe()) {
  captureFormSubmit();
}
