import throttle from 'lodash/throttle';
import delay from '../common/delay';
import fillInput from '../common/dom/fillInput';
import findLoginForm from '../common/dom/findLoginForm';
import getParentForm from '../common/dom/getParentForm';
import pressEnterOnFormInput from '../common/dom/pressEnterOnFormInput';
import { autoFillForget, autoFillGet } from '../common/runInBackground';
import { observe } from '../common/store';

const fillAndSubmit = async () => {
  const autoFillFormData = await autoFillGet();

  if (!autoFillFormData) {
    return;
  }

  const { otp, password, submit, timeout, username } = autoFillFormData;

  // cancel if auto-login request timed-out.
  // this happens when auto-login in new tab is requested from extension popup,
  // but session already exists and login form is not displayed.
  // after user logs out, login form becomes visible, and auto-login happens.
  // to avoid this, we forget auto-login request after a few seconds.
  if (timeout < Date.now()) {
    autoFillForget(['username', 'password', 'otp']);
    return;
  }

  const form = findLoginForm();

  if (!form) {
    return;
  }

  let lastFilledInput;

  if (username && form.username) {
    fillInput(form.username, username);
    autoFillForget(['username']);
    lastFilledInput = form.username;
  }

  if (password && form.password) {
    fillInput(form.password, password);
    autoFillForget(['username', 'password']);
    lastFilledInput = form.password;
  }

  const otpInput = form.username || form.password;

  if (
    otp && // we have the otp value
    !username && // we should already have submitted and forgot the username
    otpInput && // form has an input
    (!form.username || !form.password) && // form has only one input
    !otpInput.value // the input is not already filled
  ) {
    // uncheck 'remember this computer'
    getParentForm(otpInput)
      .querySelectorAll('input:checked, [aria-checked=true]')
      .forEach(checkbox => checkbox.click());

    await delay(100);

    fillInput(otpInput, otp);
    autoFillForget(['username', 'password', 'otp']);
    lastFilledInput = otpInput;
  }

  submit && lastFilledInput && pressEnterOnFormInput(lastFilledInput);
};

const autoLogin = () => {
  const listener = throttle(() => fillAndSubmit(), 1000);
  const mutationObserver = new MutationObserver(listener);
  mutationObserver.observe(document.body, { childList: true, subtree: true });
  document.addEventListener('click', listener);
  observe('autoFill').subscribe(listener);
};

export default autoLogin;
