import sortBy from '../sortBy';
import getFormInputs from './getFormInputs';

const extractFormCredentials = form => {
  const inputs = getFormInputs(form).filter(e => e.value);

  const [username] = sortBy(
    e => e.type === 'email',
    e => /@/.test(e.value) && /^[0-9a-z@._-]+$/.test(e.value),
    e => /^[0-9a-z@._-]+$/.test(e.value),
  )(inputs.filter(e => e.type !== 'password').filter(e => !/\s/.test(e.value)))
    .reverse()
    .map(e => e.value);

  const [password] = inputs
    .filter(e => e.type === 'password')
    .map(e => e.value);

  return { username, password };
};

export default extractFormCredentials;
